import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
  sanitizeUrl,
} from '@ali/4ever-utils';

export default {
  name: 'embed',

  match(node: JsonML): boolean {
    return getTagName(node) === 'embed';
  },

  convert(node: JsonML): React.ReactElement | null {
    // @ts-ignore
    const { src, name, type, size } = getAttributes(node);

    if (type === 'video') {
      if (!src) return null;

      return (
        <video controls src={sanitizeUrl(src)} title={name} data-size={size} />
      );
    }

    return null;
  },
};
